*{
  margin: 0;
  padding: 0;
}
body{
  background-color: #333;
}
.todo-container{
  width: 300px;
  min-height: 200px;
  background-color: peru;
  margin: 20px auto;

}
h1{
  text-align: center;
  color: white;
  
}
.todo-input-container{
  margin-top: 20px;
}
.todo-input{
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
input[type='text']{
  padding: 7px;
  border: none;
  outline: none;
}
button{
  padding: 4px 7px;
  font-size: 16px;
  background-color: #333;
  color: white;
  border: none;
}